$bg: #fff;
$black: #000;
$gray: #828282;
$bgGray: #F6F6F6;
@font-face {
    font-family: "GilroyBold";
    src : url(../gilroy/Gilroy-Bold.ttf); }

@font-face {
    font-family: "GilroyMedium";
    src : url(../gilroy/Gilroy-Medium.ttf); }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: GilroyBold;
  font-size: 18px;
  color: $black; }

body {
  background: $bg; }

section {
  max-width: 100vw;
  padding: 100px 20px; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.Title {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  width: 100%;
  align-content: center;
  text-align: center;
  padding-bottom: 20px;
  font-size: 34px; }

.Text {
  font-family: GilroyMedium;
  font-size: 18px;
  text-align: center;
  padding-bottom: 20px; }

header {
  padding: 0 20px;
  height: 90px;
  border-bottom: 2px solid $gray;
  ul {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    li {
      cursor: pointer;
      list-style-type: none;
      color: $gray; }
    li:hover {
      color: $black; }
    .active {
      color: $black; }
    li:active {
      outline: {} } } }


.Home {
  padding: 0 20px;
  padding-bottom: 100px;
  .HomeTop {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .HomeName {
      display: block;
      width: 20%;
      font-size: 48px;
      width: 20%; }
    .HomeBio {
      text-align: center;
      display: block;
      width: calc(100% - 42%);
      font-family: GilroyMedium;
      padding: 0 5%; }
    .HomeLang {
      width: 20%;
      font-size: 16px;
      p {
        display: block;
        transform: rotate(-90deg);
        float: right; } } }
  .HomeBottom {
    display: block;
    width: 100%;
    max-height: 400px;
    overflow: hidden;
    img {
      width: 100%;
      height: 400px;
      object-fit: cover;
      object-position: 0% 35%; } } }

.About {
  width: 100%;
  background: $bgGray;
  .WrapAbout {
    width: 60%;
    margin: 0 auto; } }

.Skills {
  .WrapSkills {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .SkillBox {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      width: 100px;
      padding-bottom: 20px;
      img {
        width: 90px;
        height: 90px;
        margin-bottom: 10px; }
      span {
        display: block;
        text-align: center;
        font-size: 14px;
        color: $gray; } } } }

.Portfolio {
  background: $bgGray;
  .WrapPortfolio {
    width: 100%;
    height: 500px;
    background: $gray; } }

.Contacts {
  .container {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .SendMail {
    cursor: pointer;
    width: 180px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background: $black;
    color: $bg;
    border: none;
    text-decoration: none;
    margin-bottom: 30px;
    span {
      color: inherit; } }
  .WrapLink {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
    a {
      padding: 0 20px;
      text-decoration: none;
      display: block;
      cursor: pointer; }
    img {
      width: 40px;
      height: 40px; } } }


footer {
  padding-bottom: 20px;
  text-align: center; }
